body {
  color: pink;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

header {
  color: #0ff;
  background-color: brown;
}









@media screen and (min-width: 375px) {
  .products-heading {
    margin-bottom: 32px;
  }

  .products-part-heading {
    color: orange;
  }
}







.reviews-modal-backdrop {
  width: 100%;
  height: 100%;
  transition: opacity var(--var-time-tr) var(--var-translate), visibility var(--var-time-tr) var(--var-translate);
  background-color: #2e2f4266;
  position: fixed;
  top: 0;
  left: 0;
}

.reviews-modal {
  min-height: 584px;
  width: 408px;
  height: 584px;
  background-color: var(--color-modal-background);
  transition: transform var(--var-time-tr) var(--var-translate);
  border-radius: 4px;
  flex-shrink: 0;
  align-items: center;
  padding: 72px 24px 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 1px #00000024, 0 1px 3px #0000001f, 0 2px 1px #0003;
}

